import * as React from 'react'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableContainer from '@mui/material/TableContainer'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import Paper from '@mui/material/Paper'
import { useNavigate } from 'react-router-dom'
import './common.css'
import { TablePagination, IconButton, Badge } from '@mui/material'
import VisibilityIcon from '@mui/icons-material/Visibility';
import PreviewIcon from '@mui/icons-material/Preview';
import AddIcon from '@mui/icons-material/Add';
import VideocamIcon from '@mui/icons-material/Videocam';
import useAuth from 'app/hooks/useAuth';
import Button from '@mui/material/Button'

const CustomTable = ({CustomHeadCell, CustomBodyCell, ...props}) => {
    const navigate = useNavigate()
    const { user } = useAuth();

    return (
        <>
            <Paper sx={{ width: '100%', overflow: 'hidden' }}>
                <TableContainer className="main-table">
                    <Table
                        sx={{ minWidth: 650 }}
                        size="small"
                        aria-label="simple table"
                        className="user-table"
                    >
                        <TableHead>
                            <TableRow>
                                <CustomHeadCell meta={props.rows.meta} />
                            </TableRow>
                        </TableHead>
                        {
                            props.rows && props.rows.length > 0 ?
                                <TableBody>
                                    {(props.rowsPerPage > 0
                                        ? props.rows.slice(
                                            props.page * props.rowsPerPage,
                                            props.page * props.rowsPerPage + props.rowsPerPage
                                        )
                                        : props.rows
                                    ).map((row, index) => {


                                        return (
                                            <TableRow
                                                key={`${row.id} - ${index}`}
                                                sx={{
                                                    '&:last-child td, &:last-child th': {
                                                        border: 0,
                                                    },
                                                }}
                                                onClick={() => {
                                                    props.onClick && props.onClick(
                                                        props.view === 'center'?
                                                        row.centerId:
                                                        (props.view === 'client' ? row.clientID : (props.view === 'counselor' ? row.clientID :row.id))
                                                    )
                                                }}
                                            // onClick={() => navigate(`/${props.view}/details/${row.id}`, {id: row.id})}
                                            >
                                                <CustomBodyCell row={row} />
                                            </TableRow>
                                        )
                                    })
                                    }
                                </TableBody>
                                :
                                <>
                                    {/* <TableRow className='no-records'> */}
                                    <caption>No records found</caption>
                                    {/* </TableRow> */}
                                </>
                        }
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[]}
                    component="div"
                    count={props?.pageDetails?.total || 0}
                    rowsPerPage={10}
                    page={props?.pageDetails?.page - 1 || 0}
                    onPageChange={props.handleChangePage}
                    onRowsPerPageChange={props.handleChangeRowsPerPage}
                    className="table-page"
                />
            </Paper>
        </>
    )
}

export default CustomTable