import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable';
import { authRoles } from 'app/auth/authRoles';
const DevicesList = Loadable(lazy(() => import('./DevicesList')));
const DeviceDetails = Loadable(lazy(() => import('app/views/CenterView/Devices/DeviceDetails')));

const devicesRoutes = [
    {
        path: '/devices',
        element: <DevicesList />,
        auth: authRoles.center
    },
    {
        path: '/devices/:id',
        element: <DeviceDetails />,
        auth: authRoles.onlyCenter
    }
]

export default devicesRoutes;