import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from '../../auth/authRoles'

const Profile = Loadable(lazy(() => import('./Profile')));
const EditProfile = Loadable(lazy(() => import('./EditProfile')));
const ChangePassword = Loadable(lazy(() => import('./ChangePassword')));
const ZoomOauth = Loadable(lazy(() => import('./ZoomAuth')))

const profileRoutes = [
    {
        path: '/profile',
        element: <Profile />,
        auth: authRoles.admin,
    },
    {
        path: '/profile/edit',
        element: <EditProfile />,
        auth: authRoles.admin
    },
    {
        path: '/changePassword',
        element: <ChangePassword />,
        auth: authRoles.admin
    },
    {
        path: '/oauth-completed',
        element: <ZoomOauth />,
        auth: authRoles.admin
    }
]

export default profileRoutes;