import React, { createContext, useEffect, useReducer, useState } from 'react'
import jwtDecode from 'jwt-decode'
import axios from 'axios.js'
import { MatxLoading } from 'app/components'
import { loginUserService } from 'app/services/authenticationService';
import { getMe } from 'app/services/userService';
import LogoutModal from 'app/components/LogoutModal/LogoutModal';

const initialState = {
    isAuthenticated: false,
    isInitialised: false,
    user: null,
}

const isValidToken = (accessToken) => {
    if (!accessToken) {
        return false
    }

    const decodedToken = jwtDecode(accessToken)
    const currentTime = Date.now() / 1000
    return decodedToken.exp > currentTime
}

const setSession = (accessToken) => {
    if (accessToken) {
        localStorage.setItem('accessToken', accessToken)
        axios.defaults.headers.common.Authorization = `Bearer ${accessToken}`
    } else {
        localStorage.removeItem('accessToken')
        delete axios.defaults.headers.common.Authorization
    }
}

const reducer = (state, action) => {
    switch (action.type) {
        case 'INIT': {
            const { isAuthenticated, user } = action.payload

            return {
                ...state,
                isAuthenticated,
                isInitialised: true,
                user,
            }
        }
        case 'LOGIN': {
            const { user } = action.payload

            return {
                ...state,
                isAuthenticated: true,
                user,
            }
        }
        case 'LOGOUT': {
            return {
                ...state,
                isAuthenticated: false,
                user: null,
                isInitialised: true
            }
        }
        case 'REGISTER': {
            const { user } = action.payload

            return {
                ...state,
                isAuthenticated: true,
                user,
            }
        }
        default: {
            return { ...state }
        }
    }
}

const AuthContext = createContext({
    ...initialState,
    method: 'JWT',
    login: () => Promise.resolve(),
    logout: () => { },
    register: () => Promise.resolve(),
})

export const AuthProvider = ({ children, handleSessionError }) => {
    const [state, dispatch] = useReducer(reducer, initialState);

    const login = async (email, password) => {
        // const response = await axios.post('/api/auth/login', {
        //     email,
        //     password,
        // })
        try {
            const data = {
                email,
                password
            }

            const response = await loginUserService(data);
            if( response.error) {
                // eslint-disable-next-line no-throw-literal
                throw response?.error?.message || 'Invalid email or password';
            }
            const { jwt, user } = response;

            setSession(jwt)

            dispatch({
                type: 'LOGIN',
                payload: {
                    user,
                },
            })
        } catch(err) {
            throw err;
        }
    }

    const register = async (email, username, password) => {
        const response = await axios.post('/api/auth/register', {
            email,
            username,
            password,
        })

        const { accessToken, user } = response.data

        setSession(accessToken)

        dispatch({
            type: 'REGISTER',
            payload: {
                user,
            },
        })
    }

    const logout = () => {
        setSession(null)
        dispatch({ type: 'LOGOUT' });
    }


    useEffect(() => {
        ; (async () => {
            try {
                const accessToken = window.localStorage.getItem('accessToken')

                if (accessToken && isValidToken(accessToken)) {
                    setSession(accessToken)
                    // const response = await axios.get('/api/auth/profile')
                    // const { user } = response.data
                    //  Call getMe API
                    let response;
                    try {
                        response = await getMe();
                        // if(response.status === 'fail'){
                        //     handleSessionError(response.message);
                        // }
                    } catch(err) {
                        console.log('err-->>', err);
                        handleSessionError('Server is not responding. You will be logged out.');
                    }

                    const user = response?.user;

                    if(user){
                        dispatch({
                            type: 'INIT',
                            payload: {
                                isAuthenticated: true,
                                user: user,
                            },
                        })
                    } else {
                        logout();
                    }
                } else {
                    dispatch({
                        type: 'INIT',
                        payload: {
                            isAuthenticated: false,
                            user: null,
                        },
                    })
                }
            } catch (err) {
                console.error(err)
                dispatch({
                    type: 'INIT',
                    payload: {
                        isAuthenticated: false,
                        user: null,
                    },
                })
            }
        })()
    }, [])


    if (!state.isInitialised) {
        return <MatxLoading />;
    }

    return (
        <>
        <AuthContext.Provider
            value={{
                ...state,
                method: 'JWT',
                login,
                logout,
                register,
            }}
        >
            {children}
        </AuthContext.Provider>
        </>
    )
}

export default AuthContext
