import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from '../../auth/authRoles'
const CreateCenter = Loadable(lazy(() => import('./CreateCenter')));
const Centers = Loadable(lazy(() => import('./Centers')));
const CenterDetails = Loadable(lazy(() => import('./CenterDetails')));

const centerRoutes = [
    {
        path: '/centers/add',
        element: <CreateCenter />,
        auth: authRoles.admin,
    },
    {
        path: '/centers/list',
        element: <Centers />,
        auth: authRoles.admin,
    },
    {
        path: '/center/details/:id',
        element: <CenterDetails />,
        auth: authRoles.admin,
    },
]

export default centerRoutes;