import { SET_ACTIVE_STEP } from "../actions/ActiveStepActions"


const initialState = {
    activeStep:0
}

const ActiveSetReducer = function (state = 0, action) {
    switch (action.type) {
        case SET_ACTIVE_STEP: {
            return action.payload
        }
        default: {
            return state
        }
    }
}

export default ActiveSetReducer
