import React, { useState, useEffect } from 'react';
import {
    Grid,
    IconButton, Badge
} from '@mui/material'
import TableCell from '@mui/material/TableCell'

import CustomTable from '../common/CustomTable';


const ClientListTable = (props) => {
    const [rowsPerPage, setRowsPerPage] = React.useState(10)
    const [page, setPage] = useState(0)

    const handleChangePage = (event, newPage) => {
        console.log(newPage)
        setPage(newPage)
        if (!props.searchResult) {
            try {
                let pageNumber = (Number(newPage)) === 0 ? 0 : (Number(newPage) - 1) + 10
                props.fetchData(newPage + 1)
                    // .then((resp) => {
                    //     setCounsellorData(formatCounsellor(resp))
                    //     // setRootRows(resp)
                    // })
                    // .catch(err => {
                    //     console.log("ERROR", err)
                    // })
            } catch (error) {
                console.log("ERROR", error)
            }
        }
    }

    // const handleChangeRowsPerPage = (event) => {
    //     setRowsPerPage(+event.target.value)
    //     setPage(0)
    // }
    // useEffect(() => {
    //     // console.log(props.data)
    //     setData(props.data)
    // }, [data])

    const CustomHeadCell = ({ meta }) => {
        const data = [];
        let entries = Object.entries(meta)
        for (const [index, [key, value]] of Object.entries(entries)) {
            key !== 'id' && data.push(<TableCell width={key === 'scheduledAt' ? 140 : 60} align="center" className="text-style" key={index}>{value}</TableCell>)
        }
        return data
    }
    

    const CustomBodyCell = ({ row }) => {
        const rows = [];
        let entries = Object.entries(row)
        for (const [index, [key, value]] of Object.entries(entries)) {
            key !== 'id' && rows.push(<TableCell className="text-style" component="th" scope="row" align="center" key={index}>{props.getCellContent(key, value)}</TableCell>)
        }
        return rows
    }
    function onClick(e) {
        console.log('onClicked', e)
        
    }
    return (
        <Grid>
            {props.role === 'Provider' ? (
                <Grid>
                    <CustomTable
                        onClick={props.onClick}
                        page={page}
                        pageDetails={props.pageDetails}
                        handleChangePage={handleChangePage}
                        rowsPerPage={rowsPerPage}
                        // handleChangeRowsPerPage={handleChangeRowsPerPage}
                        rows={props.data}
                        CustomHeadCell={CustomHeadCell}
                        CustomBodyCell={CustomBodyCell}
                    />
                </Grid>
            ) : (
                <Grid>

                </Grid>
            )}
        </Grid>
    )
}

export default ClientListTable