import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from 'app/auth/authRoles';
const Clients = Loadable(lazy(() => import('./Clients/Clients')));
const ClientDetails = Loadable(lazy(() => import('./Clients/ClientDetails')));
const Centers = Loadable(lazy(() => import('./Centers/Centers')));
const CenterDetails = Loadable(lazy(() => import('./Centers/CenterDetails')));
const UpcomingList = Loadable(lazy(() => import('./Appointments/UpcomingList')))
const HistoryList = Loadable(lazy(() => import('./Appointments/HistoryList')))
const Meetings = Loadable(lazy(() => import('./Meetings/Meetings')))

const counselorRoutes = [
    {
        path: '/clients-list',
        element: <Clients />,
        auth: authRoles.counsellor,
    },
    {
        path: '/client-details/:id',
        element: <ClientDetails />,
        auth: authRoles.counsellor,
    },
    {
        path: '/centers-list',
        element: <Centers />,
        auth: authRoles.counsellor,
    },
    {
        path: '/center-details/:id',
        element: <CenterDetails />,
        auth: authRoles.counsellor,
    },
    {
        path: '/upcoming-appointments',
        element: <UpcomingList />,
        auth: authRoles.counsellor,
    },
    {
        path: '/history-appointments',
        element: <HistoryList />,
        auth: authRoles.counsellor,
    },
    {
        path: '/meeting-session',
        element: <Meetings />,
        auth: authRoles.counsellor,
    },
]

export default counselorRoutes;