import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from '../../auth/authRoles'
const Users = Loadable(lazy(() => import('./Users')));
const AddUser = Loadable(lazy(() => import('./AddUser')));
const EditUser = Loadable(lazy(() => import('./EditCenter')));

const usersRoutes = [
    {
        path: '/users',
        element: <Users />,
        auth: authRoles.counsellor,
    },
    {
        path: '/users/add',
        element: <AddUser />,
        auth: authRoles.onlyAdmin,
    },
    {
        path: '/users/editCenter',
        element: <EditUser />,
        auth: authRoles.onlyAdmin,
    }
]

export default usersRoutes;