export const findUsers = (options) => {
    const searchData = options && options.search || '';
    const filter = options && options.filter || '';
    const page = options && options.page || 0;

    let url = `/api/users?pagination=${page}`;

    if(searchData){
        url += `&search=${searchData}`;
    }
    if(filter) {
        url += `&filters=${filter}`;
    }

    return makeRequest('GET', url)
        .then((resp) => {
            return resp;
        })
        .then((json) => {
            return json;
        }); 
}