import AuthGuard from 'app/auth/AuthGuard'
import NotFound from 'app/views/sessions/NotFound'
import chartsRoute from 'app/views/charts/ChartsRoute'
import materialRoutes from 'app/views/material-kit/MaterialRoutes'
import dashboardRoutes from 'app/views/dashboard/DashboardRoutes'
import sessionRoutes from 'app/views/sessions/SessionRoutes'
import profileRoutes from 'app/views/profile/ProfileRoutes';
import MatxLayout from '../components/MatxLayout/MatxLayout'
import { Navigate } from 'react-router-dom'
import usersRoutes from 'app/views/users/UsersRoutes'
import usersDetailsRoutes from 'app/views/userDetails/UserDetailRoutes'
import addUserRoutes from 'app/views/addUser/AddUserRiutes'
import centerRoutes from 'app/views/center/CenterRoutes';
import deviceRoutes from 'app/views/devices/DevicesRoutes';
import counsellorListRoutes from 'app/views/adminView/counsellor/list/CounsellorListRoutes'
import counsellorDetailRoutes from 'app/views/adminView/counsellor/details/CounsellorDetailRoutes'
import counsellorAddRoutes from 'app/views/adminView/counsellor/add/CounsellorAddRoutes'
import counsellorEditRoutes from 'app/views/adminView/counsellor/edit/CounsellorEditRoutes' 
import clientsRoutes from 'app/views/CenterView/ClientRoutes'
import counselorRoutes from 'app/views/counsellorView/counselorRoutes'
import devicesListRoutes from 'app/views/adminView/devices/list/DevicesListRoutes';
import calenderRoute from 'app/views/Calender/CalenderRoute'

export const AllPages = () => {
    const all_routes = [
        {
            element: (
                <AuthGuard>
                    <MatxLayout />
                </AuthGuard>
            ),
            children: [
                ...dashboardRoutes, 
                ...chartsRoute, 
                ...materialRoutes,
                ...profileRoutes,
                ...usersRoutes,
                ...usersDetailsRoutes,
                ...addUserRoutes,
                ...centerRoutes,
                ...deviceRoutes,
                ...counsellorListRoutes,
                ...counsellorDetailRoutes,
                ...counsellorAddRoutes,
                ...counsellorEditRoutes,
                ...clientsRoutes,
                ...counselorRoutes,
                ...devicesListRoutes,
                ...calenderRoute
            ],
        },
        ...sessionRoutes,
        {
            path: '/',
            element: <Navigate to="dashboard/default" />,
        },
        {
            path: '*',
            element: <NotFound />,
        },
    ]

    return all_routes
}
