import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from '../../../../auth/authRoles'
const CounsellorDetails = Loadable(lazy(() => import('./CounsellorDetails')));

const counsellorDetailRoutes = [
    {
        path: '/counsellors/details/:id',
        element: <CounsellorDetails />,
        auth: authRoles.counsellor,
    },
]

export default counsellorDetailRoutes;