import { combineReducers } from 'redux'
import ScrumBoardReducer from './ScrumBoardReducer'
import NotificationReducer from './NotificationReducer'
import EcommerceReducer from './EcommerceReducer'
import NavigationReducer from './NavigationReducer'
import ActiveSetReducer from './ActiveStepReducer'
import {BarrierReducer, ClientDetails, ClientId, ClientTreatment, ClinicianReducer, deviseLisence, noteReducer, TraumaDetails, updateClientReducer, updateNoteReducer, updateNotesReducer} from './ClientIntakeReducer'

const RootReducer = combineReducers({
    notifications: NotificationReducer,
    navigations: NavigationReducer,
    scrumboard: ScrumBoardReducer,
    ecommerce: EcommerceReducer,
    activeStep: ActiveSetReducer,
    client: ClientDetails,
    clinician: ClinicianReducer,
    trauma: TraumaDetails,
    barrier: BarrierReducer,
    clientId: ClientId,
    treatment: ClientTreatment,
    updateClient: updateClientReducer,
    isUpdatedNote: updateNoteReducer,
    notes: noteReducer,
    deviseLisence: deviseLisence
})

export default RootReducer
