import React from "react";
import { Grid, FormControl, InputLabel, Select, MenuItem, TextField } from '@mui/material'
import { Box } from '@mui/system'
// import TextField from '@mui/material/TextField'
import SearchIcon from '@mui/icons-material/Search';
import './SearchInput.css'


const SearchInput = (props) => {
    return <>
        <Grid className="user-box">
            <TextField
                label={props.label || "Search"}
                variant="outlined"
                className="user-search"
                onChange={props.handleBlur}
                placeholder={props.placeholder}
                type="text"
                value={props.value}
                autoFocus
                InputProps={{
                    startAdornment: <SearchIcon />,
                }}
            />
            {props.filterValue && (
                <Box sx={{ minWidth: '40%' }} className="select-role">
                <FormControl fullWidth>
                    <InputLabel
                        sx={{
                            backgroundColor: '#fafafa',
                            padding: '0.25rem',
                        }}
                    >{props.filterLabel}</InputLabel>
                    <Select
                        id="demo-simple-select"
                        value={props.filterValue}
                        label="Age"
                        onChange={props.handleChange}
                    >
                        <TextField
                            // label="Search Center"
                            variant="outlined"
                            className="user-filter"
                            onChange={props.fetchData}
                            placeholder="Search Center"
                            type="text"
                            value={props.centerValues}
                        // InputProps={{
                        //     startAdornment: <SearchIcon />,
                        // }}
                        />
                        <MenuItem value={'All'}>Select All</MenuItem>
                        {props.centerValues && props.centerValues.length > 0 && props.centerValues.map((item, index) => {
                            return <MenuItem key={index} value={item.id}>{item.name}</MenuItem>
                        })}
                        
                    </Select>
                </FormControl>
            </Box>
            )}
            
        </Grid>
    </>
}

export default SearchInput