import React, { useState, useEffect, useRef } from 'react'
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator'
import {
    Grid, Card, Button,
    Dialog,
    DialogContent,
    DialogContentText,
    DialogTitle,
    CircularProgress,
    Box,
    DialogActions,
} from '@mui/material'
import { styled, useTheme } from '@mui/system'
import moment from 'moment'

const TextField = styled(TextValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
}))

const StyledButton = styled(Button)(({ theme }) => ({
    width: '70%',
    marginLeft: '15%',
    fontSize: '16px',
    fontWeight: '400',
    padding: '10px !important',
    background: theme.palette.primary.main,
    color: '#fff',
    textAlign: 'center',
    "&:hover": {
        transform: 'scale(1.03)',
        background: theme.palette.primary.main,
    },
    transition: 'all 0.2s ease-out'
}))

const StyledProgress = styled(CircularProgress)(() => ({
    position: 'absolute',
    top: '6px',
    left: '25px',
}))

const CancelButton = styled(Button)(() => ({
    display: 'block',
    color: '#fff',
    // marginTop: '20px',
    width: '86px',
    backgroundColor: '#e15260',
    '&:hover': {
        backgroundColor: '#cc2333',
        color: '#fff'
    }
}))

const AddNewNotes = (props) => {
    const [clientId, setClientId] = useState('')
    const [sessionId, setSessionId] = useState('')
    // const [loading, setLoading] = useState(false)
    const [newNotes, setNewNotes] = useState('')
    const formEl = useRef();

    function handleChange(event) {
        event.persist()
        setNewNotes(event.target.value)
    }

    const closeModal = () => {
        formEl.current.resetValidations();
        setNewNotes('')
        props.handleClose();
        // navigate(-1);
    }
    useEffect(() => {
        ValidatorForm.addValidationRule('VALID_INPUT', (value) => {
            if (value && isNaN(value) && value.length == 0) {
                return false;
            }
            return true;
        });
        if (props.data) {
            setClientId(props.data.clientId)
            setSessionId(props.data.sessionId)
        }

        return () => {
            ValidatorForm.removeValidationRule('VALID_INPUT');
        }
    }, [props])
    return (
        <div>
            <Dialog
                open={props.open}
                // fullScreen={fullScreen}
                onClose={() => closeModal()}
                // style={{
                //     display: 'flex',
                //     alignItems: 'center',
                //     justifyContent: 'center',
                //     // maxHeight: '300px',
                //     // marginTop: '250px'
                // }}
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle id="form-dialog-title">Add New Note</DialogTitle>
                <ValidatorForm
                        ref={formEl}
                        onSubmit={() => props.handleSubmit(newNotes)}
                        onError={() => null}
                    >
                <DialogContent dividers={true}>
                    {/* <div>Client : {clientId}</div> */}
                    <DialogContentText>Client : {clientId}</DialogContentText>
                    {/* <Grid container> */}
                    {/* <Grid item lg={12} md={6} sm={12} xs={12} sx={{ mt: 2 }}> */}
                        <TextField
                            fullWidth
                            multiline
                            rows={3}
                            id="new_notes"
                            name="details"
                            label="Notes*"
                            type="text"
                            value={newNotes}
                            onChange={handleChange}
                            validators={['required']}
                            errorMessages={['This field is required']}
                            sx={{ mt: 2 }}
                        // inputProps={{ readOnly: true }}
                        />

                    {/* <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                                                <p>{item.added_by}</p>
                                                <p>{moment(item.updatedAt).format('DD/MM/YYYY')}</p>
                                            </div> */}
                    {/* </Grid> */}
                    {/* </Grid> */}
                    {/* </DialogContentText> */}
                </DialogContent>
                <DialogActions>
                    <Button disabled={props.loading} color="primary" variant="contained" type="submit">
                        Add Notes
                    </Button>
                    {props.loading && (
                        <StyledProgress
                            size={24}
                            className="buttonProgress"
                        />
                    )}


                    {/* </Box> */}
                    <CancelButton onClick={() => closeModal()}>
                        Cancel
                    </CancelButton>
                </DialogActions>
                </ValidatorForm>
            </Dialog>
        </div>
    )
}

export default AddNewNotes