import React from 'react'
import Box from '@mui/material/Box'
import { Grid } from '@mui/material'

const UserProfileDetail = ({ data }) => {
    const element = []
    const { verified, role, ...restData } = data
    let entries = Object.entries(restData)
    for (const [index, [key, value]] of Object.entries(entries)) {
        if (key !== 'logo' && key !== 'id') {
            element.push(
                // <TableCell align="center" className="text-style" key={index}>{value}</TableCell>
                <Box
                    key={index}
                    sx={{
                        borderTop: 1,
                        paddingTop: 1,
                        paddingBottom: 1,
                        borderColor: 'divider',
                    }}
                >
                    <Grid container>
                        <Grid item xs={6} sx={{ textTransform: 'capitalize' }}>
                            {key
                                .replace('_', ' ')
                                .replace(/([a-z])([A-Z])/g, '$1 $2')}
                        </Grid>
                        <Grid item xs={6}>
                            {value}
                        </Grid>
                    </Grid>
                </Box>
            )
        }
    }
    return element
}

export default UserProfileDetail
