import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from 'app/auth/authRoles';
const Clients = Loadable(lazy(() => import('./Clients/Clients/Clients')));
const AddClient = Loadable(lazy(() => import('./Clients/AddClient/AddClient')));
const ClientDetails = Loadable(lazy(() => import('./Clients/Clients/ClientDetails')));
const ClientList = Loadable(lazy(() => import('app/components/ClientList/ClientList')));
const Clinicians = Loadable(lazy(() => import('./Clinicians/Clinicians')));
const CliniciansDetails = Loadable(lazy(() => import('./Clinicians/CliniciansDetails')));
const AddClinician = Loadable(lazy(() => import('./Clinicians/AddClinician')));

const ReturnDeviceOrExpireLicense = Loadable(lazy(() => import('./Devices/ReturnDeviceOrExpireLicense')));

const clientsRoutes = [
    {
        path: '/clients',
        element: <Clients />,
        auth: authRoles.center,
    },
    {
        path: '/client/details/:id',
        element: <ClientDetails />,
        auth: authRoles.center,
    },
    {
        path: '/clients/add',
        element: <AddClient />,
        auth: authRoles.center,
    },
    {
        path: '/clients/list',
        element: <ClientList />,
        auth: authRoles.center,
    },
    {
        path: '/clinicians',
        element: <Clinicians />,
        auth: authRoles.center,
    },
    {
        path: '/devices/return',
        element: <ReturnDeviceOrExpireLicense />,
        auth: authRoles.center,
    },
    {
        path: '/clinicians/details/:id',
        element: <CliniciansDetails />,
        auth: authRoles.center,
    },
    {
        path: '/clinicians/add',
        element: <AddClinician />,
        auth: authRoles.center,
    }

]

export default clientsRoutes;