const API_BASE_URL = 'http://localhost:1337';

export const registerUserService = (request) => {
   
    const credentials = {
        first_name: request.user.first_name,
        last_name: request.user.last_name,
        phone_number: request.user.phone_number,
        email: request.user.signup_email,
        ren_number: request.user.ren_number,
        agency_name: request.user.agency_name,
        agency_license_number: request.user.agency_license_number,
        job_designation: request.user.job_designation,
        country: request.user.country,
        pwd: request.user.signup_password,
        created_at: request.user.created_at
    };

    return makeRequest('POST', '/auth/signup', credentials)
        .then((response) => {
            return response;
        })
        .then((json) => {
            return json;
        });
};

export const loginUserService = (request) => {
    
    const credentials = {
        identifier: request.email,
        password: request.password,
    };

    return makeRequest('POST', '/api/auth/local', credentials)
        .then((response) => {
            return response;
        })
        .then((json) => {
            return json;
        });
}

export const makeRequest = async(method, url, body, withoutAuthorization) => {
    const request_ulr = API_BASE_URL + url;
    const headers = {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        // Version: '6.0',
    };
    let access_token = '';
    if(localStorage.accessToken){
        access_token = localStorage.accessToken;
    }
    if (access_token && !withoutAuthorization) {
        headers.authorization = 'Bearer ' + access_token;
    }
    const options = { method, headers };
    if (body) {
        options.body = JSON.stringify(body);
    }
    return await fetch(request_ulr, options).then((response) => {
        return response.text().then((text) => {
            return text ? JSON.parse(text) : {};
        });
    });
};