import React, { lazy } from 'react'
import Loadable from 'app/components/Loadable/Loadable'
import { authRoles } from '../../../../auth/authRoles'
const CounsellorAdd = Loadable(lazy(() => import('./CounsellorAdd')));

const counsellorAddRoutes = [
    {
        path: '/counsellors/add',
        element: <CounsellorAdd />,
        auth: authRoles.counsellor,
    },
]

export default counsellorAddRoutes;