import {
    IS_UPDATED_NOTE,
    RESET_CLIENT,
    SET_BARRIER_DATA,
    SET_CLICIAN_DATA,
    SET_CLIENT_DATA,
    SET_CLIENT_ID,
    SET_TRAUMA_DATA,
    SET_TREATMENT_DATA,
    UPDATE_CLIENT,
    UPDATE_DEVICE_TYPE,
    UPDATE_NOTES,
} from '../actions/ClientIntakeActions'

export const ClientDetails = function (state = {}, action) {
    switch (action.type) {
        case SET_CLIENT_DATA: {
            return action.payload
        }
        case RESET_CLIENT: {
            return {}
        }
        default: {
            return state
        }
    }
}

